interface ImageCarouselProps {
    images: { url: string; alt: string }[];
    getImageUrl: (url: string) => string;
}

const ImageCarousel = ({ images, getImageUrl }: ImageCarouselProps) => {
    const [currentIndex, setCurrentIndex] = useState(0);
    const sliderRef = useRef<HTMLDivElement>(null);

    const goToSlide = (index: number) => {
        if (index < 0) {
            setCurrentIndex(images.length - 1);
        } else if (index >= images.length) {
            setCurrentIndex(0);
        } else {
            setCurrentIndex(index);
        }
    };

    const goToPrevious = () => {
        goToSlide(currentIndex - 1);
    };

    const goToNext = () => {
        goToSlide(currentIndex + 1);
    };

    useEffect(() => {
        if (sliderRef.current) {
            sliderRef.current.style.transform = `translateX(-${currentIndex * 100}%)`;
        }
    }, [currentIndex]);

    return (
        <div css={CarouselContainerStyle}>
            <div css={SliderContainerStyle}>
                <div
                    css={SliderStyle}
                    ref={sliderRef}
                >
                    {images.map((image, index) => (
                        <div key={index} css={SlideStyle}>
                            <img
                                src={getImageUrl(image.url)}
                                alt={image.alt}
                                css={OfferImageStyle}
                            />
                        </div>
                    ))}
                </div>

                {images.length > 1 && (
                    <>
                        <div
                            css={NavigationButtonStyle}
                            className="prev"
                            onClick={goToPrevious}
                        >
                            <ArrowBackIosNew fontSize="small" />
                        </div>
                        <div
                            css={NavigationButtonStyle}
                            className="next"
                            onClick={goToNext}
                        >
                            <ArrowForwardIos fontSize="small" />
                        </div>
                    </>
                )}
            </div>

            {images.length > 1 && (
                <div css={PaginationContainerStyle}>
                    {images.map((_, index) => (
                        <div
                            key={index}
                            css={PaginationDotStyle}
                            className={index === currentIndex ? 'active' : ''}
                            onClick={() => goToSlide(index)}
                        />
                    ))}
                </div>
            )}
        </div>
    );
};

export const OfferDetails = () => {
    const { id } = useParams<{ id: string }>();
    const navigate = useNavigate();
    const { t } = useTranslation();

    const [offer, setOffer] = useState<OfferDto | null>(null);
    const [loading, setLoading] = useState<boolean>(true);
    const [error, setError] = useState<string | null>(null);

    const setGeneralError = () => {
        setError("Nie udao si zaadowa oferty . Spjrz na oferty na Booksy lub skontaktuj si ze mn.")
    }

    useEffect(() => {
        // Scroll to top when component mounts
        window.scrollTo(0, 0);

        const fetchOffer = async () => {
            if (!id) {
                setGeneralError()
                console.error(`No offer ID provided`)
                setLoading(false);
                return;
            }

            // Try to find the offer in the initialOffers first
            const findFallbackOffer = () => {
                const fallbackOffer = (offersConfig as OfferDto[]).find(offer => offer.slug === id || offer.id == parseInt(id, 10));
                if (fallbackOffer) {
                    console.log(`Found offer with ID ${id} in fallback data.`);
                    setOffer(fallbackOffer as OfferDto);
                    return true;
                }
                return false;
            };

            try {
                const response = await axios.get<OfferDto>(`${url}/${id}`);
                const data = response.data;

                if (data && data.id && data.name) {
                    // @ts-ignore
                    setOffer(data.map(offer => ({ ...offer, name: b64_to_utf8(offer.name), description: b64_to_utf8(offer.description) })));
                } else {
                    console.error('Invalid data format from API. Trying fallback data.');
                    if (!findFallbackOffer()) {
                        setGeneralError();
                    }
                }
            } catch (err) {
                console.error(`API error: ${err instanceof Error ? err.message : 'Unknown error'}. Trying fallback data.`);

                // Try to get the offer from initialOffers
                if (!findFallbackOffer()) {
                    setGeneralError();
                }
            } finally {
                setLoading(false);
            }
        };

        fetchOffer();
    }, [id]);

    const handleBackClick = () => {
        // Check if we can go back in history, otherwise go to home page
        if (window.history.length > 2) {
            navigate(-1);
        } else {
            navigate('/');
        }
    };

    if (loading) {
        return (
            <div css={OfferDetailsContainerStyle}>
                <div css={LoadingStyle}>Loading offer details...</div>
            </div>
        );
    }

    if (error) {
        return (
            <div css={OfferDetailsContainerStyle}>
                <div css={ErrorStyle}>
                    <div>{error}</div>
                    <MyButton
                        text="Powrt"
                        variant="contained"
                        colorVariant="primary"
                        startIcon={<ArrowBack />}
                        onClick={handleBackClick}
                    />
                </div>
            </div>
        );
    }

    if (!offer) {
        return (
            <div css={OfferDetailsContainerStyle}>
                <div css={NotFoundStyle}>
                    <div>Nie udao si zaadowa informacji o ofercie</div>
                    <MyButton
                        text="Powrt"
                        variant="contained"
                        colorVariant="primary"
                        startIcon={<ArrowBack />}
                        onClick={handleBackClick}
                    />
                </div>
            </div>
        );
    }

    const getImageUrl = (url: string) => {
        if (url.startsWith('http')) {
            return url;
        } else {
            return `../${url}`;
        }
    }

    return (
        <>
            <OfferHelmet
                offerName={offer.name}
                offerDescription={offer.description}
                offerImage={offer.main_image.url}
            />
            <div css={OfferDetailsContainerStyle}>
                <MyButton
                    text="Powrt"
                    variant="contained"
                    colorVariant="primary"
                    startIcon={<ArrowBack />}
                    onClick={handleBackClick}
                    additionalCss={BackButtonStyle}
                />
                <div css={ContentContainerStyle}>
                    <h2 css={OfferTitleStyle}>
                        {offer.details_name || offer.name}
                    </h2>
                    <div css={DescriptionContainerStyle}>
                        <div
                            css={OfferDescriptionStyle}
                            dangerouslySetInnerHTML={{ __html: offer.description }}
                        />
                    </div>
                </div>
                <div css={ImageContainerStyle}>
                    <ImageCarousel
                        images={[
                            offer.main_image,
                            ...(offer.additional_images || [])
                        ]}
                        getImageUrl={getImageUrl}
                    />
                </div>
            </div>
        </>
    );
}; 